@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
:root {
  --main-color: #1b1d23;
  --dark-color: #888888;
  --medium-color: #7d828f;
  --light-color: #c8ccd8;
  --very-light-color: #eeeff4;
  --error-color: #df5656;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Spartan', sans-serif;
}

h1 {
  font-weight: 700;
  font-size: 80px;
  line-height: 80px;
}

h3 {
  font-weight: 700;
  font-size: 56px;
  line-height: 56px;
  letter-spacing: -2px;
  color: #1b1d23;
  color: var(--main-color);
}

a,
a:hover {
  text-decoration: none;
  color: #7d828f;
  color: var(--medium-color);
}

li {
  list-style: none;
}

.lead {
  font-size: 15px;
  font-weight: 500;
  line-height: 25px;
  width: 445px;
  color: #888888;
  color: var(--dark-color);
}

.btn {
  background: #1b1d23;
  background: var(--main-color);
  border: none;
  border-radius: 0px;
  padding: 25px 30px;
}

.btn:hover {
  background: #888888;
  background: var(--dark-color);
}

@media (max-width: 768px) {
  .container {
    max-width: 573px;
  }
  .current {
    left: -15%;
  }

  .btn {
    padding: 15px 20px;
  }
}

@media (max-width: 567px) {
  h1 {
    font-weight: 700;
    font-size: 50px;
    line-height: 50px;
  }

  h3 {
    font-size: 40px;
    line-height: 48px;
    letter-spacing: -1.48px;
    width: 311px;
  }

  .lead {
    width: 311px;
  }

  .current {
    display: none;
  }
}

@media (max-width: 376px) {
  h1 {
    font-weight: 700;
    font-size: 40px;
    line-height: 40px;
  }

  .lead {
    width: 280px;
  }
}

.Navbar {
  height: 152px;
  z-index: 99;
}

.Navbar-link-active {
  color: var(--main-color);
  position: relative;
}

.Navbar-link-active::after {
  position: absolute;
  content: '';
  top: 30px;
  left: 25%;
  width: 20px;
  height: 2px;
  background: var(--main-color);
}

@media (max-width: 567px) {
  .Navbar {
    height: 96px;
    width: 100%;
  }

  .Navbar-links {
    background: var(--very-light-color);
    -webkit-transform: translateY(38px);
            transform: translateY(38px);
    margin-left: 30px;
    text-align: left;
    font-weight: 700;
  }

  .Navbar button {
    margin-right: 10px !important;
  }

  .Navbar-link-active::after {
    display: none;
  }
}

